<template>
  <div class="studentList">
    <el-form :inline="true" :model="formInline" class="demo-form-inline" size="mini">
      <el-form-item>
        <el-button type="primary" @click="addStudent">新增</el-button>
      </el-form-item>
      <el-form-item label="姓名">
        <el-input v-model="formInline.name" placeholder="请输入姓名"></el-input>
      </el-form-item>
      <el-form-item label="学号">
        <el-input v-model="formInline.number" placeholder="请输入学号"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="find">查询</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="name" label="姓名" align="center"> </el-table-column>
      <el-table-column prop="sex" label="性别" align="center"> </el-table-column>
      <el-table-column prop="age" label="年龄" align="center"> </el-table-column>
      <el-table-column prop="number" label="学号" align="center"> </el-table-column>
      <el-table-column prop="class" label="班级" align="center"> </el-table-column>
      <el-table-column prop="state" label="状态" align="center"> </el-table-column>
      <el-table-column prop="address" label="地址" align="center"> </el-table-column>
      <el-table-column prop="phone" label="联系方式" align="center"> </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-edit"
            @click="updateInfo(scope.row)"
          ></el-button>
          <el-button
            type="danger"
            size="mini"
            icon="el-icon-delete"
            @click="deleteInfo(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="新增信息" :visible.sync="dialogFormVisible" width="500px">
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="姓名" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="formLabelWidth">
          <el-radio v-model="form.sex" label="男">男</el-radio>
          <el-radio v-model="form.sex" label="女">女</el-radio>
        </el-form-item>
        <el-form-item label="年龄" :label-width="formLabelWidth" prop="age">
          <el-input v-model="form.age" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="学号" :label-width="formLabelWidth" prop="number">
          <el-input v-model="form.number" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="班级" :label-width="formLabelWidth" prop="class">
          <el-select v-model="form.class" placeholder="请选择班级">
            <el-option label="一班" value="一班"></el-option>
            <el-option label="二班" value="二班"></el-option>
            <el-option label="三班" value="三班"></el-option>
            <el-option label="四班" value="四班"></el-option>
            <el-option label="五班" value="五班"></el-option>
            <el-option label="六班" value="六班"></el-option>
            <el-option label="七班" value="七班"></el-option>
            <el-option label="八班" value="八班"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="状态" :label-width="formLabelWidth" prop="state">
          <el-select v-model="form.state" placeholder="请选择状态">
            <el-option label="已入学" value="已入学"></el-option>
            <el-option label="未入学" value="未入学"></el-option>
            <el-option label="休学中" value="休学中"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="地址" :label-width="formLabelWidth" prop="address">
          <el-input v-model="form.address" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="联系方式" :label-width="formLabelWidth" prop="phone">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="sure(form)">确 定</el-button>
      </div>
    </el-dialog>
    <el-pagination background layout="prev, pager, next,junp" :total="1000"> </el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      formInline: {
        name: '',
        number: ''
      },
      dialogFormVisible: false,
      form: {
        name: '',
        sex: '男',
        age: '',
        number: '',
        class: '',
        state: '',
        address: '',
        phone: ''
      },
      formLabelWidth: '80px',
      rules: {
        name: [{ required: true, message: '请输入姓名' }],
        age: [{ required: true, message: '请输入年龄' }],
        number: [{ required: true, message: '请输入学号' }],
        class: [{ required: true, message: '请输入班级' }],
        state: [{ required: true, message: '请输入状态' }],
        address: [{ required: true, message: '请输入地址' }],
        phone: [{ required: true, message: '请输入电话' }]
      }
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      this.service
        .get('/students')
        .then((res) => {
          console.log(res)
          this.tableData = [...res.data.data]
        })
        .catch((err) => {
          console.log(err)
        })
    },
    updateInfo(row) {
      console.log(row)
    },
    deleteInfo(row) {
      console.log(row)
    },
    addStudent() {
      this.dialogFormVisible = true
    },
    find() {},
    sure(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          console.log(this.state)
          const a = true
          if (a) {
            console.log(this.form)
            this.service
              .post('/students', this.form)
              .then((res) => {
                console.log(res)
                this.dialogFormVisible = false
                this.form = {
                  name: '',
                  sex: '1',
                  age: '',
                  number: '',
                  class: '',
                  state: '',
                  address: '',
                  phone: ''
                }
                this.getData()
              })
              .catch((err) => {
                console.log(err)
              })
          } else {
            // 调用修改接口
          }
        } else {
          console.error(this.form)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.studentList {
  .demo-form-inline,
  .el-form-item {
    text-align: left;
    .el-select {
      width: 100%;
    }
  }
  .dialog-footer {
    text-align: center;
  }
}
.el-pagination {
  text-align: right;
  margin-top: 20px;
}
</style>
